*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .heading_style{
    padding: 30px 0;
    text-align: center;
    font-family: "cursive";
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
    font-size: 1.5rem;
    margin-bottom: 30px;
    background-color: white;
    color: black;
  
  }
  
  /* .cards{
    width: 100%;
    height: auto;
  }
  .heading{
    color: aqua;
  } */
  
  .card{
    margin: 6%;
    transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
    background-color: white;
    width: 21.25;
    border-radius: 13px;
    box-shadow: 0px 14px 10px -7px rgba(0,0,0,0.1);
    float: left;
  }
  
  .card__img{
    width: 100%;
    height: 235px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    padding: 16px 24px 24px 24px;
    background-color: white;  
  
  }
  
  .card__info{
    background-color: white;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    padding: 16px 24px 24px 24px;
  }
  
  .card__category{
    text-transform: uppercase;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: gray;
    background-color: white;
  
  }
  
  .card__title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: "cursive";
    background-color: white;
    text-transform: capitalize;
  
  }
  
  button{
    padding: 5px 8px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    outline: none;
    border: 3px solid black;
    background-color: white;
    color: #868686;
    cursor: pointer;
  
  } 
  
  *{
      margin: 0px;
      padding: 0px;
      background-color: white;
      
  }
  
  .heading_style{
      margin-top: 0;
      padding: 20px 20px;
      background-color: darkgray;
      color: darkcyan;
      text-align: center;
      font-weight: bold;
      font-style: oblique;
      font-family: 'Montserrat', sans-serif;
  }
    
  .cards{
      margin:30px 80px;
      display:inline-block;
      align-items: center;
      justify-content: center;
  
  }
  
    .card{
          margin: 10px;
          width: 100%;
          height:100%;
          border-radius: 5px;  
          box-shadow: 2px 2px 10px rgb(43, 44, 44); 
    }
  
    .card:hover{
        transition: .5s all ease-in-out;
        transform: scale(.8);
    }
  
  
  
    img{
        width:100%;
        height:20vh;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
  
            
    }
    .card_info{
        font-size: 20px;
        font-style: italic;
        text-align: center;
       
    }
    h3{
      text-align: center;
      margin-top: 10px;
      font-size: 20px;
      font-weight: bold;
  }
  
  button{
      padding: 5px;
      margin-top: 10px;
      margin-bottom: 10px;
      border: 1px solid black;
      border-radius: 5px;
      cursor: pointer;
  }
  button:hover{
      background-color: black;
      color: white;
      transition: 0.5s;
  
  }